<script>
	import Vue from 'vue'
	export default {
		onLaunch: function() {},
		onShow() {
			// 开启线上调试
			// uni.setEnableDebug({
			//     enableDebug: false
			// })


			const options = {
				title: '开启消息通知',
				content: '开启后可及时收到重要通知，是否前往设置开启？',
				confirmText: '去设置',
				cancelText: '暂不开启',
				successToast: '通知权限已开启，感谢支持！',
				failToast: '您未开启通知权限，可能错过重要消息',
				successCallback: () => {},
				failCallback: () => {}
			}

			uni.getSetting({
				withSubscriptions: true,
				success: (res) => {
					if (!res.subscriptionsSetting.mainSwitch) {
						// 显示订阅消息引导弹窗
						uni.showModal({
							title: options.title,
							content: options.content,
							confirmText: options.confirmText,
							cancelText: options.cancelText,
							success: (res) => {
								if (res.confirm) {
									uni.openSetting({
										success: (settingRes) => {
											if (uni.getSystemInfoSync()
												.platform === 'wechat') {
												if (settingRes.authSetting[
														'scope.subscribeMessage'
													]) {
													uni.showToast({
														title: options
															.successToast,
														icon: 'success'
													})
													options.successCallback()
												} else {
													uni.showToast({
														title: options
															.failToast,
														icon: 'none'
													})
													options.failCallback()
												}
											}
										},
										fail: (err) => {
											console.error('打开设置失败', err)
											options.failCallback()
										}
									})
								} else {
									uni.showToast({
										title: '您可以随时在设置中开启通知',
										icon: 'none'
									})
									options.failCallback()
								}
							},
							fail: (err) => {
								console.error('显示弹窗失败', err)
								options.failCallback()
							}
						})


					} else {

					}
				},
				fail: (err) => {
					console.error('获取设置失败', err)
				}
			})



			const updateManager = uni.getUpdateManager()
			// 请求完新版本信息的回调
			updateManager.onCheckForUpdate(res => {
				if (res.hasUpdate) {
					// 新版本下载成功
					updateManager.onUpdateReady(() => {
						uni.showModal({
							title: '更新提示',
							content: '新版本已经准备好，点击确定重启小程序',
							success(res) {
								if (res.confirm) {
									// 新的版本已经下载好，强制更新
									updateManager.applyUpdate()
								}
							}
						})
					})
				}
			})
			// 新版本下载失败
			updateManager.onUpdateFailed(res => {
				console.error(res)
			})


		},


		onHide: function() {
			console.log('App Hide')
		}
	}
</script>

<style lang="scss">
	.icon {
		width: 1em;
		height: 1em;
		vertical-align: -0.15em;
		fill: currentColor;
		overflow: hidden;
	}

	/*每个页面公共css */
	@import '@/uni_modules/uni-scss/index.scss';
	/* #ifndef APP-NVUE */
	@import '@/static/customicons.css';
	// 设置整个项目的背景色
	@import "@/static/iconfont.css";



	page {
		background-color: #f5f5f5;
	}

	/* #endif */
	.example-info {
		font-size: 14px;
		color: #333;
		padding: 10px;
	}

	// 隐藏滚动条
	::-webkit-scrollbar {
		width: 0 !important;
	}

	::-webkit-scrollbar {
		width: 0 !important;
		height: 0;
	}


	.icon {
		width: 1em;
		height: 1em;
		vertical-align: -0.15em;
		fill: currentColor;
		overflow: hidden;
	}
</style>